import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import { FiX, FiMenu } from "react-icons/fi";
import {
  FaTwitter,
  FaInstagram,
  FaFacebookF,
  FaLinkedinIn,
} from "react-icons/fa";

const SocialShare = [
  { Social: <FaFacebookF />, link: "https://www.facebook.com/" },
  { Social: <FaLinkedinIn />, link: "https://www.linkedin.com/" },
  { Social: <FaInstagram />, link: "https://www.instagram.com/" },
  { Social: <FaTwitter />, link: "https://twitter.com/" },
];

class HeaderFour extends Component {
  constructor(props) {
    super(props);
    this.menuTrigger = this.menuTrigger.bind(this);
    this.CLoseMenuTrigger = this.CLoseMenuTrigger.bind(this);
    //  this.subMetuTrigger = this.subMetuTrigger.bind(this);
    window.addEventListener("load", function () {
      console.log("All assets are loaded");
    });
  }
  menuTrigger() {
    document.querySelector(".header-wrapper").classList.toggle("menu-open");
  }
  CLoseMenuTrigger() {
    document.querySelector(".header-wrapper").classList.remove("menu-open");
  }
  render() {
    var elements = document.querySelectorAll(".has-droupdown > a");
    for (var i in elements) {
      if (elements.hasOwnProperty(i)) {
        elements[i].onclick = function () {
          this.parentElement
            .querySelector(".submenu")
            .classList.toggle("active");
          this.classList.toggle("open");
        };
      }
    }

    const { logo, color, headerPosition } = this.props;
    let logoUrl;
    if (logo === "light") {
      logoUrl = <img src="/assets/images/logo/logo-light.png" alt="Trydo" />;
    } else if (logo === "dark") {
      logoUrl = <img src="/assets/images/logo/logo-dark.png" alt="Trydo" />;
    } else if (logo === "symbol-dark") {
      logoUrl = (
        <img src="/assets/images/logo/logo-symbol-dark.png" alt="Trydo" />
      );
    } else if (logo === "all-dark") {
      logoUrl = <img src="/assets/images/logo/logo-all-dark.png" alt="Trydo" />;
    } else if (logo === "symbol-light") {
      logoUrl = (
        <img src="/assets/images/logo/logo-symbol-light.png" alt="Trydo" />
      );
    } else {
      logoUrl = <img src="/assets/images/logo/logo.png" alt="Trydo" />;
    }

    return (
      <header
        className={`header-area formobile-menu ${headerPosition} ${color}`}
      >
        <div className="header-wrapper" id="header-wrapper">
          <div className="header-left">
            <div className="logo">
              <a href="/">{logoUrl}</a>
            </div>
          </div>
          <div className="social-share-inner d-none d-sm-block">
            <ul className="social-share social-style--2 color-black d-flex justify-content-start liststyle">
              {SocialShare.map((val, i) => (
                <li key={i}>
                  <a href={`${val.link}`}>{val.Social}</a>
                </li>
              ))}
            </ul>
          </div>
          <div className="header-right">
            <nav className=" new mainmenunav d-lg-block">
              <ul className="mainmenu">
                <li className="">
                  <NavLink exact to="/" >
                    Home
                  </NavLink>
                </li>
                <li>
                  <NavLink exact to="/about" activeClassName="active">
                    About Us
                  </NavLink>
                </li>
                <li>
                  <NavLink exact to="/oursectors" activeClassName="active">
                    Our Sectors
                  </NavLink>
                </li>

                <li className="has-droupdown">
                  <NavLink exact to="/integrated-facility-management-services">Our Services</NavLink>
                  <ul className="submenu">
                    <li>
                      {" "}
                      <NavLink
                      exact
                        to="/integrated-facility-management-services"
                        activeClassName="active"
                      >
                        Integrated Facility Management
                      </NavLink>{" "}
                    </li>
                    <li>
                      {" "}
                      <NavLink exact
                        to="/waste-management-services"
                        activeClassName="active"
                      >
                        Waste Management Services
                      </NavLink>{" "}
                    </li>
                    <li>
                      {" "}
                      <NavLink exact
                        to="/clubhouse-maintenance-services"
                        activeClassName="active"
                      >
                        Clubhouse Maintenenace Services
                      </NavLink>{" "}
                    </li>
                    <li>
                      {" "}
                      <NavLink exact
                        to="/frontend-guest-relations"
                        activeClassName="active"
                      >
                        Front-End Guest relations
                      </NavLink>{" "}
                    </li>
                    <li>
                      {" "}
                      <NavLink exact to="/mechanical-services" activeClassName="active">
                        Mechanical,Plumbing,Elcetrical {"&"} ORM
                      </NavLink>{" "}
                    </li>
                    <li>
                      {" "}
                      <NavLink exact
                        to="/catering-management-services"
                        activeClassName="active"
                      >
                        Catering Management Services
                      </NavLink>{" "}
                    </li>
                    <li>
                      {" "}
                      <NavLink exact
                        to="/pest-control-services"
                        activeClassName="active"
                      >
                        Pest Control Services
                      </NavLink>{" "}
                    </li>
                    <li>
                      {" "}
                      <NavLink exact
                        to="/garden-landscaping-services"
                        activeClassName="active"
                      >
                        Garden {"&"} landscaping Services
                      </NavLink>{" "}
                    </li>
                    <li>
                      {" "}
                      <NavLink exact
                        to="/housekeeping-services"
                        activeClassName="active"
                      >
                        Housekeeping Services
                      </NavLink>{" "}
                    </li>
                    <li>
                      {" "}
                      <NavLink exact to="/security-services" activeClassName="active">
                        Security Services
                      </NavLink>{" "}
                    </li>
                    <li>
                      {" "}
                      <NavLink exact
                        to="/production-support-services"
                        activeClassName="active"
                      >
                        Production Supporting Services
                      </NavLink>{" "}
                    </li>
                    <li>
                      {" "}
                      <NavLink exact
                        to="/management-supporting-services"
                        activeClassName="active"
                      >
                        Management Supporting Services
                      </NavLink>{" "}
                    </li>
                    <li>
                      {" "}
                      <NavLink exact
                        to="/engineering-solutions-services"
                        activeClassName="active"
                      >
                        Engineering Solutions
                      </NavLink>{" "}
                    </li>
                  </ul>
                </li>

                <li>
                  <NavLink exact to="/contact" activeClassName="active">
                    Contact Us
                  </NavLink>
                </li>
              </ul>
            </nav>
            <div className="header-btn d-none d-sm-block">
              <a
                className="btn-default btn-border btn-opacity"
                target=""
                href="/download"
              >
                <span>Download Our App</span>
              </a>
            </div>
            {/* Start Humberger Menu  */}
            <div className="humberger-menu d-block d-lg-none pl--20">
              <span
                onClick={this.menuTrigger}
                className="menutrigger text-white"
              >
                <FiMenu />
              </span>
            </div>
            {/* End Humberger Menu  */}
            <div className="close-menu d-block d-lg-none">
              <span onClick={this.CLoseMenuTrigger} className="closeTrigger">
                <FiX />
              </span>
            </div>
          </div>
        </div>
      </header>
    );
  }
}
export default HeaderFour;
