import React, { Component } from "react";
import { FiCheck } from "react-icons/fi";
import PageHelmet from "../component/common/Helmet";
import ScrollToTop from "react-scroll-up";
import { FiChevronUp } from "react-icons/fi";
import Header from "../component/header/HeaderFour";
import Footer from "../component/footer/Footer";
import Breadcrumb from "./common/Breadcrumb";

class Download extends Component {
  constructor() {
    super();
    this.state = {
      isOpen: false,
    };
    this.openModal = this.openModal.bind(this);
  }
  openModal() {
    this.setState({ isOpen: true });
  }
  render() {
    return (
      <React.Fragment>
        {/* Start Pagehelmet  */}
        <PageHelmet pageTitle="Download Our App" />
        {/* End Pagehelmet  */}

        <Header
          headertransparent="header--transparent"
          color="color-black"
          logoname="logo.png"
        />

        {/* Start Breadcrump Area */}
      <div className="rn-page-title-area pt--120 pb--190 bg_image bg_image--15"  data-black-overlay="6">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="rn-page-title text-center pt--100">
                                    <h2 className="title theme-gradient">Cntact With Us</h2>
                                    <p>Contrary to popular belief, Lorem Ipsum is not simply random text. </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
        {/* End Breadcrump Area */}

        {/* Start Page Wrapper */}
        <div className="rn-service-details ptb--120 bg_color--1">
          <div className="container">
            <div className="row">
              <div className="col-lg-12">
                <div className="service-details-inner">
                  <div className="inner">
                    {/* Start Single Area */}
                    <div className="row row--35 sercice-details-content align-items-center">
                      <div className="col-lg-6 col-12">
                        <div className="thumb position-relative">
                          <img
                            className="w-100"
                            src="/assets/images/service/app.png"
                            alt="Service Images"
                          />
                        </div>
                      </div>
                      <div className="col-lg-6 col-12">
                        <div className="details mt_md--30 mt_sm--30">
                          <div className="section-title">
                            <h2 className="title">Download Our App!</h2>
                            <p className="description">
                              Book your service with rento integrated facility
                              management services.
                            </p>
                            <p className="description">
                              Get our application on multi-platform!
                            </p>
                            <a className="footerpng" href="#button">
                              {" "}
                              <img
                                className="w-20000"
                                src="/assets/images/service/3.png"
                                alt="Service Images"
                              />
                            </a>
                            <a className="footerpng" href="#buttons">
                              {" "}
                              <img
                                className="w-20000"
                                src="/assets/images/service/4.png"
                                alt="Service Images"
                              />
                            </a>
                          </div>
                          <ul className="liststyle mt--30">
                            <li>24*7 Customer Support</li>
                            <li>Optimized Performance</li>
                            <li>Multi-Payment Gateways for Local/International Transactions.</li>
                          </ul>
                        </div>
                      </div>
                    </div>
                    {/* End Single Area */}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* End Page Wrapper */}

        {/* Start Back To Top */}
        <div className="backto-top">
          <ScrollToTop showUnder={160}>
            <FiChevronUp />
          </ScrollToTop>
        </div>
        {/* End Back To Top */}

        <Footer />
      </React.Fragment>
    );
  }
}
export default Download;
